@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Roboto:wght@100;300;400;500;700&display=swap');
@import "normalize.css";

@mixin flex($parameter1,$parameter2,$parameter3,$parameter4) {
  display: $parameter1;
  flex-direction: $parameter2;
  justify-content: $parameter3;
  align-items: $parameter4;
}

@mixin wider-margin {
  margin: 0 100px
}

;
@mixin align-right {
  justify-content: flex-end;
}

@mixin align-left {
  justify-content: flex-start
}

;

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffff;
  font-family: 'Roboto', sans-serif;
}

.container {
  &__align {
    @include flex(flex, row, space-between, center);
    height: 48.6px;
    margin: 0 100px;

    &--footer {
      justify-content: center;
    }
  }

  &--left {
    padding: 0px 15px;
    @include flex(flex, row, space-between, center);
    justify-content: flex-start;
  }

  &--right {
    @include flex(flex, row, space-between, center);
    justify-content: flex-end;
    padding: 0px 15px;
  }

  &--right a {
    @include flex(flex, row, space-between, center);
  }
}

.info {
  display: inline-block;
  height: 100%;
  text-align: center;
  font-size: 11px;
  padding: 0px 15px 0px 0px;
  text-transform: uppercase;
  font-weight: 300;
}

.header {
  &__top {
    background-color: #fef7f6;
    height: 48.6px;
    font-size: 11px;
    width: 100%;
  }

  &__lower {
    font-size: 11px;
    width: 100%;
  }
}

.navigation-bar {
  height: 90.4px;
  @include wider-margin;
  @include flex(flex, row, space-between, center);

  &__item {
    @include flex(flex, row, space-between, center);

    &--element {
      position: relative;
      display: flex;
      align-items: flex-start;
      justify-content: center;

      :hover.icons-header {
        color: #ff5353;
      }
    }
  }
}

.cloud {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background-color: #ff5353;
  color: #fef7f6;
  font-size: 8px;
}

.icons-header {
  text-decoration: none;
  @include align-right;
  display: flex;
  align-items: flex-start;
}

.sky {
  position: absolute;
  top: 0;
  right: 0;
}

.logo {
  position: relative;
  display: inline;
}

.menu {
  list-style: none;
  position: relative;
  display: flex;
  flex-direction: row;

  &__item {
    display: block;
    color: black;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    padding: 0 15px;
    font-size: 13px;
    text-transform: uppercase;
  }

  :hover.menu__item {
    color: #ff5353;
  }
}

.dropbtn {
  padding: 35px 15px;
  border: none;
  cursor: pointer;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  left: 0;
  background-color: white;
  min-width: 220px;
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 7%);
  z-index: 1;
  list-style: none;
  transition: all 300ms ease;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.dropdown-content a {
  color: black;
  padding: 10px 10px;
  text-decoration: none;
  display: block;

}

.dropdown-content a:hover {
  background-color: #f1f1f1;
  color: #ff5353;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  color: #ff5353;
}

.banner {
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  background-image: url("https://demo.themeies.com/silon/images/bg-1.jpg");


  &__fluid {
    max-height: 700px;
    position: relative;
    margin-right: auto;
    margin-left: auto;
    display: flex;

    &--2 {
      max-height: 500px;
      background-color: #e9e2d8;
    }
  }

  &__column1 {
    width: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    &--content {
      width: 100%;
      position: absolute;
      left: 60%;
      top: 50%;
      transform: translate(-50%, -50%);
      @include flex(flex, column, space-around, center);
    }
  }

  &__column2 {
    position: relative;
    display: flex;
    justify-content: flex-end;
    width: 50%;
  }
}

.content__button:hover.content__button--content {
  color: blue;
  background-color: white;
}

.content {
  position: relative;
  text-align: center;
  padding: 15px 15px;
  @include flex(flex, column, space-between, center);
  flex-wrap: nowrap;

  &__button {
    background: #ff5353;
    width: 170px;
    height: 51.2px;
    border-radius: 8px;

    &--content {
      display: inline-block;
      text-transform: uppercase;
      color: #ffff;
      padding: 20px 25px;
      text-align: center;
      text-decoration: none;
      margin-left: auto;
      margin-right: auto;
      font-size: 14px;
      font-weight: 500;
    }
  }
}

.banner__first--image {
  max-height: 100%;
}

.banner__colored--image {
  max-height: 100%;
}

p.tagline {
  height: 11.2px;
  text-transform: uppercase;
  color: #565656;
  margin: 0;
}

